.movie-container {
  padding-top: 3vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: black;
  border-bottom: 0.1vmin solid white;
  border-top: 0.1vmin solid white;
}

@media screen and (max-width: 800px) {
  .movie-container {
    padding-top: 10vw;
  }
}
